/**
  * Top Banner.
**/
#banner {
  width: 100%;
  height: 100vh;
  text-align: center;
}
#banner .banner-wrapper {
  padding-top: 20vh;
}
#banner .big-text {
  display: block;
  font-weight: 600;
  font-size: 8.5em;
  letter-spacing: 0px;
  -webkit-font-smoothing: antialiased;
  font-family: gibsonbold;
  text-transform: uppercase;
}
#banner .small-text {
  display: block;
  font-size: 2em;
  text-transform: uppercase;
}
#banner .button-wrapper {
  margin-top: 5em;
}
#work .work-component-image {
  max-width: 100%;
}
#work .work-article {
  display: none;
}
#blog .blog-component-image {
  max-width: 100%;
}
/**
  * Dividers.
**/
.fbs-divider {
  min-height: 200px;
  background-color: lightgray;
  width: 100%;
  margin: 50px 0;
}
.fbs-divider .undertitle {
  font-size: 1.5em;
  font-weight: 200;
  -webkit-font-smoothing: antialiased;
}
/**
  * Typography
**/
h1 {
  font-weight: 600;
  font-size: 4em;
  line-height: 1.5;
  font-family: gibsonbold;
  letter-spacing: -2px;
  -webkit-font-smoothing: antialiased;
}
/**
  * Buttons.
**/
.button {
  display: inline-block;
  padding: 20px 60px;
  color: black;
  border: 2px solid black;
  font-size: 0.95em;
  font-weight: 600;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.button .medium {
  padding: 10px 30px;
  font-size: 0.85em;
}
.button .small {
  padding: 5px 15px;
  font-size: 0.75em;
}
.button .border {
  color: #d7d7d7;
  border: 2px solid #d7d7d7;
  background: none;
}
.button:hover {
  background: #56c16b;
  border: 2px solid #56c16b;
}
.border.button:hover {
  color: #48a65b;
  border: 2px solid #48a65b;
  background: none;
}
a.button:hover {
  text-decoration: none;
}
